<template>
  <div class="tax-page param">
    <h4>参数设置</h4>
    <div class="content">
      <div class="options">
        <span>设置计税需要的参数,只要税法有调整，请变更新版本后修改</span>
        <div class="btns">
          <div class="version" v-if="this.version != null">
            <a v-if="this.hasPrev" @click="prevVersion"><i class="iconfont icon-jiantou-left"></i></a><span>{{version.name}}</span>
            <a v-if="this.hasNext" @click="nextVersion"><i class="iconfont icon-jiantou-right"></i></a>
          </div>
          <el-button-group>
            <el-button type="primary" @click="addParam">添加</el-button>
            <el-button type="primary" @click="addVersion">版本化</el-button>
          </el-button-group>
        </div>
      </div>
      <table class="table sample-table table-bordered table-hover">
        <thead>
        <tr>
          <th scope="col" width="220">参数名称</th>
          <th scope="col">参数值</th>
          <th scope="col" width="80">自定义</th>
          <th scope="col" width="220">配置</th>
        </tr>
        </thead>
        <draggable v-model="params" :element="'tbody'" @end="onEnd">
        <tr v-for="item in params" :key="item.id">
          <td><span v-for="label in item.labels.split(',')" class="type" v-if="label">{{label}}</span>{{item.name}}</td>
          <td>{{item.config}}{{item.unit}}</td>
          <td><el-switch v-model="item.customFlag" active-color="#13ce66" @change="customChange(item)"></el-switch></td>
          <td>
            <span class="area-params">
              <span v-for="a in item.areas" :key="a.id">{{a.areaName}},</span>
            </span>
            <swak-link :to="'/system/param/update/' + item.id" class="ops">配置</swak-link>
          </td>
        </tr>
        <tr v-if="params.length == 0">
          <td class="td-none" colspan="4">点击添加，设置参数</td>
        </tr>
        </draggable>
      </table>
    </div>
  </div>
</template>

<script>
  import SwakLink from "../../ui/components/link/link";
  import {Version, Param} from '@/api/system/param';
  import Draggable from 'vuedraggable';

  export default {
    components: {SwakLink,Draggable},
    name: "tax-param",
    data() {
      return {
        version: null,
        hasPrev: true,
        hasNext: false,
        params: [],
        value5 :true
      }
    },
    mounted() {
      this.$emit('layout:load', "param")

      // get newest version
      this.loadVersion()
    },
    methods: {
      loadVersion: function () {
        Version.newest().then(data => {
          this.changeVersion(data.obj)
        })
      },
      changeVersion: function (version) {
        this.version = version;
        if (this.version) {
          this.loadParams(version.id)
        }
      },
      customChange: function(param) {
        param.custom = param.customFlag?1:0;
        Param.custom(param)
      },
      loadParams: function (version) {
        Param.list(version).then(data => {
          var params = data.obj || [];
          params.forEach(p => {
            p.areas = p.areas || [];
          })
          this.params = params
          this.loadParamAreas();
        });
      },
      loadParamAreas: function () {
        this.params.forEach((p) => {
          Param.areas(p.id).then(res => {
            p.areas = res.obj || []
          })
        })
      },
      prevVersion: function () {
        var id = this.version.id;
        Version.prev(id).then(data => {
          if (data.obj != null) {
            this.changeVersion(data.obj)
            this.hasNext = true;
          } else {
            this.$notify.info("没有上一个版本了");
            this.hasPrev = false;
          }
        })
      },
      nextVersion: function () {
        var id = this.version.id;
        Version.next(id).then(data => {
          if (data.obj != null) {
            this.changeVersion(data.obj)
            this.hasPrev = true;
          } else {
            this.$notify.info("没有下一个版本了");
            this.hasNext = false;
          }
        })
      },
      addVersion: function () {
        this.$confirm("确认添加新的版本?", "操作提醒", {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          Version.build().then(res => {
            if (res.obj) {
              this.$notify.success("创建版本成功")
              this.loadVersion();
            } else {
              this.$notify.error("创建版本失败，每个月只能创建一个版本")
            }
          })
        }).catch(() => {
        })
      },
      addParam: function () {
        var versionId = this.version ? this.version.id : '';
        this.$router.push({
          path: '/system/param/add',
          query: {
            versionId: versionId
          }
        })
      },
      onEnd: function () {
        var params = [];
        this.params.forEach((item, index) => {
          item.sort = index;
          params.push({
            id: item.id,
            sort: item.sort
          })
        })
        var json = JSON.stringify(params)
        Param.sort(json).then(res => {
         this.$notify.success("排序成功！")
        })
      }
    }
  }
</script>

